/* STATIC STYLES (desktop-first) */

.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 80%;
  gap: 3rem;
}

.about-card {
  padding: 2rem;
  text-align: center;
}
.about-card .about-icon {
  color: var(--color-primary);
  margin-bottom: 1rem;
}
.about-card h5 {
  margin-bottom: 1rem;
}
.about-card small {
  color: var(--color-primary-text-dark);
}
.about-card p {
  color: var(--color-primary-text-dark);
  margin: 2rem;
  text-align: center;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .about-cards {
    width: 95%;
  }
}

@media (max-width: 992px) {
  .about-cards {
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .about-cards {
    grid-template-columns: repeat(1, 1fr);
    width: 20rem;
    gap: 3rem;
  }
}

@media (max-width: 576px) {
  .about-cards {
    width: 15rem;
    gap: 2rem;
  }
}