/* STATIC STYLES (desktop-first) */

.card1 {
  background: var(--color-secondary-dark);
  border: 1px solid transparent;
  border-radius: 1rem;
  transition: all 400ms ease;
}

.card1:hover {
  background: transparent;
  border-color: var(--color-secondary);
  cursor: default;
}

