/* STATIC STYLES (desktop-first) */

.portfolio-container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
}

.portfolio-item-desktop {
  display: flex;
  flex-direction: column;
}

.portfolio-item {
  display: grid;
  grid-template-columns: 40% 60%;
  align-items: center;
  padding: 2.5rem;
  max-width: 85vw;
}
.portfolio-item .portfolio-description {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  width: 90%;
}
.portfolio-item h3 {
  text-align: center;
}
.portfolio-item h4 {
  color: var(--color-primary-text-dark);
  text-align: center;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
  .portfolio-item {
    display: flex;
    flex-direction: column;
  }

  .portfolio-item .portfolio-description {
    margin-top: 0;
    margin-bottom: 2rem;
  }
}

