/* STATIC STYLES (desktop-first) */

.card3 {
  background: var(--color-secondary-dark);
  border: 1px solid transparent;
  border-radius: 2rem;
  transition: all 400ms ease;
  transform: perspective(750px) translate3d(0px, 0px, -100px) rotateX(25deg) scale(0.95, 1);
}

.card3:hover {
  background: var(--color-blur);
  border-color: var(--color-secondary);
  transform: translate3d(0px, 40px, -120px);
}

